.author {
    display: flex;
    align-items: center;
    color: transparentize($white, 0.5);
    padding: 10px 20PX;
    opacity: 0.75;

    &__former {
        flex: 1;
    }

    &__latter {
        flex: none;
        display: flex;
        align-items: center;
    }

    &__link {
        width: 20px;
        height: 20px;
        opacity: 0.5;
        transition: opacity 500ms;

        img {
            width: 100%;
        }
    }

    @include hasHover() {
        &__link:hover {
            opacity: 1;
        }
    }


    &__link+&__link {
        margin-left: 10px;
    }
}